* {
	margin: 0;
	padding: 0;
}

#loader {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
}
body {
    
    background: -webkit-linear-gradient(1deg, rgb(209, 212, 30) 0%, rgb(47, 206, 47) 100%);
    
    background: linear-gradient(89deg, rgb(209, 212, 30) 0%, rgb(47, 206, 47) 100%); /* w3c */
    font-family: 'Orbitron', sans-serif;
   
  }
.particles{
    position:fixed;
    top:0;
    right:0;
    bottom:0;
    left:0;
    z-index:-1;

}

